body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
  width: 80%;
  margin: 1rem auto;
  box-shadow: 0 0 1rem grey;
  display: flex;
  justify-content: space-evenly;
  flex-flow: row wrap;
}


.wrap {
    display: flex;
    justify-content: space-evenly;
    flex-flow: row wrap;
  }
  
  .form {
    margin: 1rem auto;
    width: 40%;
    text-align: center;
    display: flex;
    justify-content: space-evenly;
  }
  
  .form input {
    padding: .5rem;
  }
  
  .btn {
    display: block;
  }
  
  .card {
    margin: 1rem 0;
    box-shadow: 0 0 1rem grey;
    text-align: center;
    padding: 1rem;
    flex-basis: 20%;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: space-between;
  }
  
  .card h3 {
    color: grey;
  }

  .viePost{
    background: #98c3f0;
    color: white;
    padding: 10px;
    border-radius: 25px;
    text-decoration: none;

  }

